'use client';

import { useState, useEffect } from 'react';
import Head from 'next/head';
import axios from 'axios';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// Register Chart.js components
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

export default function Dashboard() {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [attendanceSummary, setAttendanceSummary] = useState([]);
  const [currentMonth] = useState(new Date().getMonth() + 1); // 1-12
  const [currentYear] = useState(new Date().getFullYear());

  // Fetch attendance data for the current month
  const fetchAttendanceData = async () => {
    setLoading(true);
    try {
      const loggedInCity = localStorage.getItem('loggedInCity');
      if (!loggedInCity) {
        setError('No city selected. Please log in again.');
        setLoading(false);
        return;
      }
      const response = await axios.get('http://localhost:3080/monthly-summary', {
        params: { month: currentMonth, year: currentYear, city: loggedInCity },
      });
      const { attendanceSummary } = response.data;
      setAttendanceSummary(attendanceSummary);
    } catch (error) {
      console.error('Error fetching attendance data:', error);
      setError(error.response?.data?.error || error.message);
      setAttendanceSummary([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchAttendanceData();
  }, []);

  // Process attendance data for the bar chart
  const attendanceChartData = () => {
    const attendanceByName = attendanceSummary.reduce((acc, item) => {
      const name = item.name || 'Unknown';
      acc[name] = (acc[name] || 0) + 1; // Count each day as 1
      return acc;
    }, {});

    const labels = Object.keys(attendanceByName);
    const data = Object.values(attendanceByName);

    return {
      labels: labels,
      datasets: [
        {
          label: 'Days Present',
          data: data,
          backgroundColor: 'rgba(0, 0, 0, 0.8)', // Black bars
          borderColor: 'rgba(0, 0, 0, 1)',
          borderWidth: 1,
        },
      ],
    };
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
        labels: {
          color: '#000000', // Black text for legend
        },
      },
      title: {
        display: true,
        text: `Attendance for ${new Date(0, currentMonth - 1).toLocaleString('default', { month: 'long' })} ${currentYear}`,
        color: '#000000', // Black text for title
      },
    },
    scales: {
      x: {
        ticks: {
          color: '#000000', // Black text for x-axis
        },
        grid: {
          color: 'rgba(0, 0, 0, 0.1)', // Light grid lines
        },
      },
      y: {
        beginAtZero: true,
        ticks: {
          color: '#000000', // Black text for y-axis
          stepSize: 1, // Whole numbers for days
        },
        grid: {
          color: 'rgba(0, 0, 0, 0.1)', // Light grid lines
        },
      },
    },
  };

  const navigateToPage = (path) => {
    window.location.href = path;
  };

  return (
    <div className="min-h-screen bg-white flex flex-col items-center p-6">
      <Head>
        <title>Police Dashboard</title>
        <script src="https://cdn.tailwindcss.com"></script>
      </Head>

      {/* Header */}
      <h1 className="text-3xl font-bold text-black mb-8 text-center">Police Dashboard</h1>

      {/* Navigation Boxes */}
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 max-w-4xl w-full">
        {/* Attendance Box */}
        <div
          onClick={() => navigateToPage('/Attendance')}
          className="bg-gray-100 border border-black rounded-lg p-6 flex flex-col items-center justify-center hover:bg-gray-200 cursor-pointer transition"
        >
          <h2 className="text-xl font-semibold text-black mb-2">Attendance</h2>
          <p className="text-black text-center">Manage and submit attendance records for officers.</p>
        </div>

        {/* Report Box */}
        <div
          onClick={() => navigateToPage('/HRrepo')}
          className="bg-gray-100 border border-black rounded-lg p-6 flex flex-col items-center justify-center hover:bg-gray-200 cursor-pointer transition"
        >
          <h2 className="text-xl font-semibold text-black mb-2">Report</h2>
          <p className="text-black text-center">View and download monthly checkpoint and attendance summaries.</p>
        </div>

        {/* Officers Box */}
        <div
          onClick={() => navigateToPage('/viewpolice')}
          className="bg-gray-100 border border-black rounded-lg p-6 flex flex-col items-center justify-center hover:bg-gray-200 cursor-pointer transition"
        >
          <h2 className="text-xl font-semibold text-black mb-2">Officers</h2>
          <p className="text-black text-center">View, edit, and manage officer details.</p>
        </div>

        {/* Payments Box */}
        <div
          onClick={() => navigateToPage('/payments')}
          className="bg-gray-100 border border-black rounded-lg p-6 flex flex-col items-center justify-center hover:bg-gray-200 cursor-pointer transition"
        >
          <h2 className="text-xl font-semibold text-black mb-2">Payments</h2>
          <p className="text-black text-center">Manage payments and financial records.</p>
        </div>
      </div>

      {/* Attendance Chart */}
      <div className="mt-12 w-full max-w-4xl">
        {loading ? (
          <p className="text-black text-center">Loading attendance data...</p>
        ) : error ? (
          <p className="text-red-600 text-center">{error}</p>
        ) : attendanceSummary.length > 0 ? (
          <div className="bg-white shadow-lg rounded-lg p-6 border border-black">
            <h2 className="text-xl font-semibold text-black mb-4">Attendance Overview</h2>
            <div className="w-full h-80">
              <Bar
                data={attendanceChartData()}
                options={chartOptions}
              />
            </div>
          </div>
        ) : (
          <p className="text-black text-center">No attendance data available for this month.</p>
        )}
      </div>
    </div>
  );
}